:root {
  --body-font-size: 16px;
  --body-font: "Istok", system-ui, -apple-system, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  --body-font-alt: Georgia, Times, Times New Roman, serif;
  --body-line-height: 161%;
  --body-color: #fff;
  --body-background: #df604a;
  --table-header-font-weight: 700;
  --table-cell-padding: 5px 10px;
  --heading-font-weight: 700;
  --heading-line-height: 1.2;
  --heading-margin: 0 0 1em;
  --paragraph-margin: 0 0 1em;
  --list-margin: 1em 0;
  --h1: 28px;
  --h2: 24px;
  --h3: 20px;
  --h4: 18px;
  --h5: 16px;
  --bold-font-weight: 700;
  --text-small: 14px;
  --main: var(--body-color);
  --red: #F61001;
  --link-color: #203735;
  --link-color-hover: #37605C;
  --swiper-theme-color: var(--main);
  --swiper-preloader-color: var(--swiper-theme-color);
  --swiper-wrapper-transition-timing-function: initial;
}

@font-face {
  font-family: "Istok";
  src: url("../fonts/IstokWeb-Regular.woff2") format("woff2"), url("../fonts/IstokWeb-Regular.woff") format("woff"), url("../fonts/IstokWeb-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Istok";
  src: url("../fonts/IstokWeb-Bold.woff2") format("woff2"), url("../fonts/IstokWeb-Bold.woff") format("woff"), url("../fonts/IstokWeb-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
