.b-btn {
  font-size: 20px;
  font-weight: 400;
  line-height: 1.25;
  position: relative;
  display: inline-flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: center;
  box-sizing: border-box;
  min-height: 49px;
  padding: 10px 20px;
  user-select: none;
  transition: background 0.3s ease, color 0.3s ease, border-color 0.3s ease;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 10px;
  text-transform: uppercase;
}
.b-btn:hover, .b-btn:focus-within, .b-btn.focus {
  outline: 0;
}
.b-btn.disabled, .b-btn:disabled {
  cursor: default;
  opacity: 0.45;
  background: inherit;
  box-shadow: none;
}
.b-btn > svg {
  display: block;
  flex: 0 0 auto;
}
.b-btn > svg:first-child {
  margin-right: 6px;
}
.b-btn > svg:last-child {
  margin-left: 6px;
}
.b-btn--big {
  font-size: 18px;
  min-height: 59px;
  padding: 4px 30px;
}
.b-btn--small {
  font-size: 15px;
  min-height: 39px;
  padding: 4px 16px;
}
.b-btn--primary {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
