// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 1.25;
$btn-border-width          : 1px;
$btn-padding               : 10px 20px;
$btn-font-size             : 20px;
$btn-border-radius         : 10px;
$btn-transition            : background .3s ease, color .3s ease, border-color .3s ease;
$btn-height                : 49px;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 4px 30px;
$btn-font-size-big         : 18px;
$btn-height-big            : 59px;

// Small
$btn-padding-small         : 4px 16px;
$btn-font-size-small       : 15px;
$btn-height-small          : 39px;

// COLOR
// White
$btn-white-bg              : #DCDCDE;
$btn-white-color           : #0B081B;
$btn-white-bg-hover        : #BDBDBF;
$btn-white-color-hover     : #0B081B;

// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	font-size       : $btn-font-size;
	font-weight     : $btn-font-weight;
	line-height     : $btn-line-height;
	position        : relative;
	display         : inline-flex;
	align-items     : center;
	flex-flow       : row nowrap;
	justify-content : center;
	box-sizing      : border-box;
	min-height      : $btn-height;
	padding         : $btn-padding;
	user-select     : none;
	transition      : $btn-transition;
	text-align      : center;
	vertical-align  : middle;
	white-space     : nowrap;
	text-decoration : none;
	border          : $btn-border-width solid transparent;
	border-radius   : $btn-border-radius;
	text-transform  : uppercase;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		cursor     : default;
		opacity    : .45;
		background : inherit;
		box-shadow : none;
	}

	&:active,
	&.active {

	}

	> svg {
		display : block;
		flex    : 0 0 auto;

		&:first-child {
			margin-right : 6px;
		}

		&:last-child {
			margin-left : 6px;
		}
	}

	// SIZE
	// Big
	&--big {
		font-size  : $btn-font-size-big;
		min-height : $btn-height-big;
		padding    : $btn-padding-big;
	}

	&--small {
		font-size  : $btn-font-size-small;
		min-height : $btn-height-small;
		padding    : $btn-padding-small;
	}

	// COLOR
	// Red
	&--primary {
		box-shadow       : 0 4px 4px 0 rgba(0, 0, 0, 0.25);
		background-color : #FF4200;
		font-weight      : 700;
		font-size        : 20px;
		color            : #FFF;
		padding          : 5px 20px;
		@media screen and (min-width : $break-desktop) {
			padding : 10px 20px;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {

		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {

			}
		}
	}

}




